<script>
import { ref } from "vue";
import { scrollTo } from "vue-scrollto";
import AOS from "aos";
import "aos/dist/aos.css";

export default {
  name: "PortfolioPage",
  components: {},
  setup() {
    const showCardContent = ref(false);

    const scrollToTop = () => {
      scrollTo("#top");
    };

    const scrollToElement = (id) => {
      scrollTo(`#${id}`);
    };

    const handleCardClick = () => {
      showCardContent.value = !showCardContent.value;
    };

    const handleCardMouseEnter = () => {
      showCardContent.value = true;
    };

    const handleCardMouseLeave = () => {
      showCardContent.value = false;
    };

    const handleCardTouchStart = () => {
      showCardContent.value = true;
    };

    const handleCardTouchEnd = () => {
      showCardContent.value = false;
    };

    const handleCardTouchCancel = () => {
      showCardContent.value = false;
    };

    const initAOS = () => {
      AOS.init();
    };

    return {
      showCardContent,
      scrollToTop,
      scrollToElement,
      handleCardClick,
      handleCardMouseEnter,
      handleCardMouseLeave,
      handleCardTouchStart,
      handleCardTouchEnd,
      handleCardTouchCancel,
      initAOS,
    };
  },
  mounted() {
    this.initAOS();
  },
};
</script>

